import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonModule, NgClass, NgIf } from '@angular/common';
import {
  ChangePersonalInfoInterface,
  DeliveryAddress,
  InitialAddress,
  PersonalInfo,
} from '@sales/delivery/data-access';
import { AddressFormFieldComponent } from '@sales/address-form-field/feat-address-form-field';
import { ChangeDeliveryPersonalInfoComponent } from './change-personal-info/change-delivery-personal-info.component';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { AssetService } from '@telenet/ng-lib-page';
import { stepImage } from './constants/change-delivery.const';
import { AnalyticsClickEventDirective } from '@sales/events/feat-analytics';

@Component({
  standalone: true,
  imports: [
    NgIf,
    NgClass,
    CommonModule,
    ReactiveFormsModule,
    AddressFormFieldComponent,
    ChangeDeliveryPersonalInfoComponent,
    TranslateModule,
    InlineSVGModule,
    AnalyticsClickEventDirective,
  ],
  selector: 'tg-sales-change-delivery-info',
  templateUrl: './change-delivery-info.component.html',
})
export class ChangeDeliveryInfoComponent implements OnInit {
  @Input() personalInfo: PersonalInfo | null | undefined;
  @Input() deliveryAddress: DeliveryAddress | null | undefined;
  @Input() validatePersonalInfoOnInit = false;
  @Output() closeOverlay = new EventEmitter();
  @Output() saveNewPersonalInfo = new EventEmitter<ChangePersonalInfoInterface>();

  isSubmitted = false;

  stepImage: string | undefined;
  form: FormGroup = new FormGroup({});

  constructor(private readonly formBuilder: FormBuilder) {
    this.stepImage = AssetService.getImagePath('HARDWARE_CHECKOUT', stepImage);
  }

  ngOnInit(): void {
    this.initForm();
  }

  onSubmit() {
    this.isSubmitted = true;
    const personalInfo = this.form.get('personalinfo');
    const deliveryAddress = this.form.get('address');
    if (this.form.valid) {
      this.saveNewPersonalInfo.next({
        personalInfo: !personalInfo?.pristine ? personalInfo?.value : null,
        deliveryAddress: !deliveryAddress?.pristine ? deliveryAddress?.value : null,
      });
    } else {
      personalInfo?.updateValueAndValidity();
      deliveryAddress?.updateValueAndValidity();
    }
  }

  private initForm() {
    if (!this.deliveryAddress) {
      throw new Error('Delivery address is not defined.');
    }

    // Validate required properties are not undefined or empty
    const requiredProperties: (keyof InitialAddress)[] = [
      'addressUnit',
      'country',
      'houseNumber',
      'municipality',
      'postalCode',
      'street',
    ];
    const hasAllRequired = requiredProperties.every(
      (prop) => this.deliveryAddress && this.deliveryAddress[prop] !== undefined
    );
    if (!hasAllRequired) {
      throw new Error('Missing required delivery address properties.');
    }

    const initialAddress: InitialAddress = {
      addressUnit: this.deliveryAddress.addressUnit,
      country: this.deliveryAddress.country,
      houseNumber: this.deliveryAddress.houseNumber,
      municipality: this.deliveryAddress.municipality,
      postalCode: this.deliveryAddress.postalCode,
      street: this.deliveryAddress.street,
      // Optional properties can remain directly assigned
      box: this.deliveryAddress.box,
      subHouseNumber: this.deliveryAddress.subHouseNumber,
      countryCode: this.deliveryAddress.countryCode,
    };

    this.form = this.formBuilder.group({});
    this.form.addControl('personalinfo', new FormControl(this.personalInfo));
    this.form.addControl('address', new FormControl(initialAddress));
  }
}
